      "Twitch Plays Pokmon (TPP) stands as an innovative example of net/web art, showing the relationship between technology, interactivity, and collective creativity. This project started in 2014 where it redefined the boundaries of traditional gaming by using the power of the internet to allow thousands of users to simultaneously control a single game of Pokmon. Through its unique approach, TPP expands the meaning of gaming and evolved into a net/web art form that engages with people on conceptual, aesthetic, and technical levels. This essay aims to examine Twitch Plays Pokmon within the context of net/web art, exploring its innovative use of the internet as a medium for artistic expression.",
      "First, we need to know what Twitch Plays Pokmon is. Twitch Plays Pokmon was when viewers could type commands in a chat box on the Twitch streaming service to control the game Pokmon. Each command was like a vote, and the game would do whatever command got the most votes. It was crazy and chaotic because there were so many people typing commands, the game became a chaotic but fascinating experience. Twitch Plays Pokmon began as an experiment by an anonymous programmer who combined a special program with a Pokmon game emulator. They did this because they wanted to see if they could make a game that thousands of people could play together at the same time.",
      "Now we need to we why Twitch Plays Pokmon is a net/web art. Twitch Plays Pokmon represents net art through its innovative and collaborative approach to gameplay. The project, which allowed thousands of Twitch users to collectively control a single game of Pokmon through chat commands, is an indication of net art as NetSpecific describes 'Net art is an elusive and sometimes anarchic art form which uses the Internet as its primary material' (NetSpecific 2024)this applies to the chaotic yet captivating nature of the Twitch experiment, as it is harnessing the power of the internet and allowing for unpredictable nature of online interactions, Twitch Plays Pokmon uses the internet and Twitch as a canvas for communal expression. Furthermore, NetSpecific discusses how early net artists addressed the best ideals of the internet, which represents Twitch Plays Pokmon as it is used for the collective nostalgia and shared experiences of a generation that knows the Pokmon industry. Additionally, the concept of hacking, cloning, and copying (NetSpecific 2024) in net art highlights the unconventional gameplay mechanics of Twitch Plays Pokmon, where users change the game's mechanics in different ways, challenging traditional gaming conventions. Showing us that Twitch Plays Pokmon is a form of net art by uses the internet as a platform for collective creativity, social interaction, and exploration of digital culture.",
