      "Objective: Create a To-Do List application that allows users to add items, mark them as completed, and visually differentiate completed items.",
      "Key Learnings from Exercise 1:",
      "1. State Management with useState: This exercise reinforced the importance of state management in React. By using the `state` hook, I learned how to manage and update the state of the to-do list dynamically.",
      "2. Event Handling: Implementing functions to handle the submission of new to-dos and the marking of items as completed helped me understand event handling in React.",
      "3. Conditional Rendering: Applying conditional rendering to visually indicate completed items taught me how to dynamically change the appearance of components based on their state.",
      "Exercise 2: Fetching Data on Component Mount and Clean-Up",
      "Objective: Implement a component that fetches data from an API when mounted and cleans up properly if unmounted before the data loads.",
      "Key Learnings From Exercise 2:",
      "1. Data Fetching with useEffect: Using the `useEffect` hook to perform side effects like data fetching when a component mounts was crucial. This exercise highlighted the lifecycle of React components and how to handle side effects.",
      "2. AbortController for Clean-Up: Understanding how to use `AbortController` to cancel an ongoing fetch request if the component unmounts were vital.",
      "3. Error Handling: Incorporating error handling in the fetch logic taught me the importance of making applications resilient to failures.",
      "Exercise 3: Nested Routing and URL Parameters",
